<template>
  <div class="appSen">
    <h3 class="h3"></h3>
    <div class="appSenCon width">
      <ul>
        <li
          :class="indexs==i?'active':''"
          @click="handle(i)"
          v-for="(item,i) in listTitle"
          :key="i"
        >{{item.title}}</li>
      </ul>
      <div class="appContent">
        <div class="appItem" v-for="(items,is) in listData" :key="is">
          <template v-if="show">
            <div class="appItemText">
              <div class="textItem" v-for="(item1,i1) in listData[is].child" :key="i1">
                <i></i>
                <h4>{{item1.title}}</h4>
                <p>{{item1.content}}</p>
              </div>
            </div>
            <div class="appItemImg">
              <img :src="items.icon" alt srcset />
            </div>
          </template>
        </div>

        <div class="appItem" v-for="(item,i) in listData1" :key="i">
          <template v-if="show1">
            <div class="appItemImg">
              <img :src="item.icon" alt srcset />
            </div>
            <div class="appItemText">
              <div class="textItem" v-for="(item1,i1) in listData1[i].child" :key="i1">
                <i></i>
                <h4>{{item1.title}}</h4>
                <p>{{item1.content}}</p>
              </div>
            </div>
          </template>
        </div>

        <template>
          <div class="appItem" v-if="show2">
            <div class="appItemCon">
              <div class="itemApp" v-for="(item,i) in listData2" :key="i">
                <h3>{{item.title}}</h3>
                <div class="imgs">
                  <img v-for="(item1,i1) in item.child" :key="i1" :src="item1.imgs" alt srcset />
                </div>
              </div>
            </div>
          </div>
        </template>
        <template>
          <div class="appItem" v-if="show3">
            <div class="appItemList">
              <div class="appItemListItem" v-for="(item,i) in listData3" :key="i">
                <p>{{item.title}}</p>
              </div>
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  components: {},
  props: {},
  data() {
    return {
      indexs: 0,
      show: true,
      show1: false,
      show2: false,
      show3: false,
      listTitle: [
        { id: 1, title: '' },
        { id: 2, title: '' },
        { id: 3, title: '' },
        { id: 4, title: '' },
      ],
      listData: [
        {
