                '',
            },
            {
              title: '',
              content: '',
            },
            {
              title: '',
              content:
                '',
            },
            {
              title: '',
              content:
                '',
            },
            {
              title: '',
              content:
                '',
            },
            {
              title: '',
              content:
                '',
            },
            {
              title: '',
              content:
                'WebAppH5',
            },
          ],
        },
      ],
      listData1: [
        {
          id: 1,
          icon: require('../assets/images/u1824.png'),
          child: [
            {
              title: '',
              content:
                '',
            },
            {
              title: '',
              content:
                'OEE',
            },
            {
              title: '',
              content:
                '',
            },
            {
              title: '',
