            { imgs: require('../assets/images/u1831.png') },
            { imgs: require('../assets/images/u1832.png') },
            { imgs: require('../assets/images/u1834.png') },
            { imgs: require('../assets/images/u1836.png') },
          ],
        },
        {
          title: '',
          child: [{ imgs: require('../assets/images/u1841.png') }],
        },
        {
          title: '',
