        },
        {
          title: '',
        },
      ],
    }
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  methods: {
    handle(i) {
      this.indexs = i
      switch (i) {
        case 0:
          this.show = true
          this.show1 = false
          this.show2 = false
          this.show3 = false
          break
        case 1:
          this.show = false
          this.show1 = true
          this.show2 = false
          this.show3 = false
          break
        case 2:
          this.show = false
          this.show1 = false
          this.show2 = true
          this.show3 = false
          break
        case 3:
          this.show = false
          this.show1 = false
          this.show2 = false
          this.show3 = true
          break
        default:
          //
          break
      }
    },
  },
}
