<!--  -->
<template>
  <div class="release">
    <!--  -->
    <TopImg
      :title="topImg.title"
      :content="topImg.content"
      :image="topImg.bgImg"
      :bl="topImg.bl"
      :aLink="topImg.aLink"
      :blName="topImg.blName"
    ></TopImg>
    <div class="barPath width">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/release' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!--  -->
    <div class="fastSoo width">
      <div class="fastSooTitle">
        <div class="sooType clearfix">
          <div class="tt"></div>
          <span
            :class="i==index?'active':''"
            v-for="(item,i) in sooListOne"
            :key="i"
            :index="i"
            @click="oneTitle(item.id,i)"
          >{{item.name}}</span>
        </div>
        <div class="genre clearfix">
          <div class="tt"></div>
          <span
            :class="item.id==index1?'active':''"
            v-for="(item,i) in sooListTwo"
            :key="i"
            :index1="i"
            @click="twoTitle(item.id,i)"
          >{{item.name}}</span>
        </div>
        <div class="way clearfix">
          <div class="tt"></div>
          <span
            :class="i==index2?'active':''"
            v-for="(item,i) in sooListThree"
            :key="i"
            :index2="i"
            @click="threeTitle(item.id,i)"
          >{{item.name}}</span>
        </div>
        <div class="way clearfix">
          <div class="tt"></div>
          <span
            :class="i==index3?'active':''"
            v-for="(item,i) in sooListFour"
            :key="i"
            :index3="i"
            @click="fourTitle(item.id,i)"
          >{{item.name}}</span>
        </div>
        <div class="sooInput">
          <el-input placeholder="" v-model="soo.names">
            <template slot="append">
              <el-button type="primary" @click="sousuo()"></el-button>
            </template>
          </el-input>
        </div>
      </div>

      <div class="appItemList">
        <div class="appItem" v-for="(item,i) in dataList.data" :key="i">
          <div class="appItemTitle">
            <span></span>
            <strong>{{item.title}}</strong>
          </div>
          <div class="appItemCon">
            <div class="pp">
              <strong></strong>
              <span>{{item.corporate}}</span>
            </div>
            <div class="pp">
              <strong></strong>
              <span v-if="item.type == 1"></span>
              <span v-if="item.type == 2"></span>
              <span v-if="item.type == 3"></span>
            </div>
            <div class="pp">
              <strong></strong>
              <span>{{item.add_time | formatDate4}}</span>
            </div>
            <div class="pp">
              <strong></strong>
              <span>{{item.end_time | formatDate4}}</span>
            </div>
            <div class="pp">
              <strong></strong>
              <span v-if="item.status == 4"></span>
              <span v-if="item.status == 5"></span>
              <span v-if="item.status == 6"></span>
              <span v-if="item.status == 7"></span>
            </div>
            <div class="pp">
              <el-button type="primary" @click="goto(item.id)"></el-button>
            </div>
          </div>
        </div>

        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="soo.pages"
          :page-size="10"
          layout="total,prev, pager, next, jumper"
          :total="dataList.total"
          prev-text=""
          next-text=""
        ></el-pagination>
      </div>
    </div>
    <!--  -->
    <div class="el-footer">
      <Footer />
    </div>
  </div>
</template>

<script>
import TopImg from '../components/TopImg'
import Footer from '../components/Footer.vue'
export default {
  name: '',
  components: { TopImg, Footer },
  props: {},
  data() {
    return {
      topImg: {
        title: '',
        bgImg: require('../assets/images/u2717.jpg'),
        content:
          '  ',
        bl: true,
        aLink: 'issue',
        blName: '',
      },
      soo: {
        pages: 1,
        names: '',
        type: '',
        status: '',
        addtime: '',
        endtime: '',
      },
      dataList: [],
      sooListOne: [
        { id: 0, name: '' },
        { id: 1, name: '' },
        { id: 2, name: '' },
        { id: 3, name: '' },
      ],
      sooListTwo: [
        { id: 0, name: '' },
        { id: 5, name: '' },
        { id: 6, name: '' },
        { id: 7, name: '' },
        { id: 4, name: '' },
      ],
      sooListThree: [
        { id: 0, name: '' },
        { id: 1, name: '' },
        { id: 2, name: '7' },
        { id: 3, name: '15' },
        { id: 4, name: '' },
        { id: 5, name: '' },
      ],
      sooListFour: [
        { id: 0, name: '' },
        { id: 1, name: '' },
        { id: 2, name: '7' },
        { id: 3, name: '15' },
        { id: 4, name: '' },
        { id: 5, name: '' },
      ],
      index: 0,
      index1: 0,
      index2: 0,
      index3: 0,
      priceIndex: 0,
      countIndex: 0,
    }
  },
  computed: {},
  watch: {},
  created() {
    this.getListData()
  },
  mounted() {},
  methods: {
    async getListData() {
      const res = await this.$http.post('/demandList', this.soo)
      console.log(res)
      this.dataList = res.data
    },
    async oneTitle(i, j) {
      this.index = j
      this.soo.type = i
      this.getListData()
    },
    async twoTitle(i, j) {
      this.index1 = j
      this.soo.status = i
      this.getListData()
    },
    async threeTitle(i, j) {
      this.index2 = j
      this.soo.addtime = i
      this.getListData()
    },
    async fourTitle(i, j) {
      this.index3 = j
      this.soo.endtime = i
      this.getListData()
    },
    async sousuo(i, j) {
      this.getListData()
    },
    goto(i) {
      this.$router.push({
        name: 'releaseDetails',
        params: {
          //params 
          ids: i,
        },
      })
    },
    handleSizeChange(val) {
      // console.log(` ${val} `)
    },
    handleCurrentChange(val) {
      // console.log(`: ${val}`)
      this.soo.pages = val
      this.getListData()
    },
  },
}
</script>

<style lang="less" scoped>
.barPath {
  height: 48px;
  line-height: 48px;
  background-color: #fff;
  padding-left: 20px;
  box-sizing: border-box;
  margin-bottom: 10px;
  .el-breadcrumb {
    line-height: 48px;
    font-size: 16px !important;
  }
}
.fastSoo {
  .fastSooTitle {
    background-color: #fff;
    padding: 10px;
    margin-bottom: 10px;
    .sooType,
    .genre,
    .way {
      margin: 10px 0;
      .tt {
        float: left;
        max-width: 80px;
      }
      span {
        float: left;
        padding: 0 20px;
        text-align: center;
        cursor: pointer;
      }
      span:hover {
        color: #0079fe;
      }
      .active {
        color: #0079fe;
      }
    }
    .sooInput {
      width: 600px;
      margin: 20px 0;
    }
  }

  .appItemList {
    .appItem {
      background-color: #fff;
      margin-bottom: 10px;
      padding: 10px 20px;
      .appItemTitle {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 10px;
        margin-top: 10px;
        span {
          flex: 1;
          max-width: 80px;
          font-weight: 700;
          font-size: 16px;
        }
        strong {
          flex: 1;
          font-size: 16px;
          color: #1677ff;
        }
      }
      .appItemCon {
        display: flex;
        align-items: center;
        margin-bottom: 30px;
        .pp {
          flex: 1;
          display: flex;
          strong {
            flex: 1;
            max-width: 80px;
          }
        }
      }
    }
  }
}
.el-pagination {
  text-align: center;
  padding: 20px 0;
  background-color: #fff;
}
</style>
